import type { ImproveCropSuggestionsOutput } from "@/ai/flows/improve-crop-suggestions";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { CheckCircle, AlertTriangle, Info } from "lucide-react"; // Added icons

interface PredictionResultsProps {
  results: ImproveCropSuggestionsOutput | null;
  error?: string | null;
}

// Helper to determine confidence level based on reasons/actions
const getConfidenceLevel = (reasons: string[], actions: string[]): 'High' | 'Medium' | 'Low' => {
  const reasonCount = reasons.length;
  const actionCount = actions.length;

  if (reasonCount >= 3 && actionCount <= 1) return 'High';
  if (reasonCount >= 2 && actionCount <= 2) return 'Medium';
  return 'Low';
}

// Helper to get icon based on confidence
const getConfidenceIcon = (level: 'High' | 'Medium' | 'Low') => {
  switch (level) {
    case 'High': return <CheckCircle className="h-5 w-5 text-green-500" />;
    case 'Medium': return <Info className="h-5 w-5 text-yellow-500" />;
    case 'Low': return <AlertTriangle className="h-5 w-5 text-red-500" />;
    default: return null;
  }
}

export function PredictionResults({ results, error }: PredictionResultsProps) {
  if (error) {
    return (
      <Card className="border-destructive bg-destructive/10">
        <CardHeader>
          <CardTitle className="text-destructive flex items-center gap-2">
             <AlertTriangle className="h-5 w-5" /> Error Generating Predictions
          </CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-destructive">{error}</p>
           <p className="mt-2 text-sm text-muted-foreground">Please try refining your input or try again later.</p>
        </CardContent>
      </Card>
    );
  }

  if (!results || results.improvedSuggestions.length === 0) {
    return (
       <Card className="border-dashed border-muted">
         <CardHeader>
           <CardTitle className="text-muted-foreground">No Predictions Yet</CardTitle>
         </CardHeader>
         <CardContent>
           <p className="text-muted-foreground">Enter your farm details above and click "Predict Suitable Crops" to see suggestions.</p>
         </CardContent>
       </Card>
    );
  }

  return (
    <div className="space-y-4">
       <h2 className="text-2xl font-semibold tracking-tight">Prediction Results</h2>
      {results.improvedSuggestions.map((suggestion) => {
         const confidence = getConfidenceLevel(suggestion.reasons, suggestion.suggestedActions);
         const confidenceIcon = getConfidenceIcon(confidence);

         return (
          <Card key={suggestion.crop} className="shadow-md hover:shadow-lg transition-shadow duration-200">
            <CardHeader>
              <div className="flex justify-between items-start">
                 <div>
                  <CardTitle className="text-xl">{suggestion.crop}</CardTitle>
                  <CardDescription>Potential suitability analysis</CardDescription>
                 </div>
                 <Badge variant={confidence === 'High' ? 'default' : confidence === 'Medium' ? 'secondary' : 'destructive'} className="ml-auto flex items-center gap-1">
                   {confidenceIcon}
                   {confidence} Confidence
                 </Badge>
              </div>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <h3 className="text-md font-semibold mb-1">Reasons for Suggestion:</h3>
                <ul className="list-disc list-inside space-y-1 text-sm text-muted-foreground">
                  {suggestion.reasons.map((reason, index) => (
                    <li key={`reason-${index}`}>{reason}</li>
                  ))}
                </ul>
              </div>
              <div>
                <h3 className="text-md font-semibold mb-1">Suggested Actions to Increase Confidence:</h3>
                 {suggestion.suggestedActions.length > 0 ? (
                    <ul className="list-disc list-inside space-y-1 text-sm text-muted-foreground">
                    {suggestion.suggestedActions.map((action, index) => (
                        <li key={`action-${index}`}>{action}</li>
                    ))}
                    </ul>
                 ) : (
                    <p className="text-sm text-muted-foreground italic">No specific actions recommended, indicating higher confidence.</p>
                 )}
              </div>
            </CardContent>
          </Card>
         )
      })}
    </div>
  );
}
