
"use client";

import * as React from "react";
import type * as z from "zod";
import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Loader2, MapPin, Sprout, CloudSun, BookOpen, BrainCircuit, LocateFixed } from "lucide-react"; // Added icons
import { CropFormSchema } from "@/schemas/crop-form-schema";
import { reverseGeocode, type ReverseGeocodeInput } from "@/ai/flows/reverse-geocode"; // Import the new flow
import { useToast } from "@/hooks/use-toast";

interface CropFormProps {
  onSubmit: (values: z.infer<typeof CropFormSchema>) => void;
  isLoading: boolean;
}

export function CropForm({ onSubmit, isLoading }: CropFormProps) {
  const [isFetchingLocation, setIsFetchingLocation] = React.useState(false);
  const { toast } = useToast();
  const form = useForm<z.infer<typeof CropFormSchema>>({
    resolver: zodResolver(CropFormSchema),
    defaultValues: {
      location: "",
      desiredCrops: "",
      soilType: "",
      climateConditions: "",
      historicalYieldData: "",
      otherParameters: "",
    },
  });

  const handleGetCurrentLocation = async () => {
    if (!navigator.geolocation) {
      toast({
        title: "Geolocation Error",
        description: "Geolocation is not supported by your browser.",
        variant: "destructive",
      });
      return;
    }

    setIsFetchingLocation(true);
    toast({
      title: "Fetching Location...",
      description: "Getting your current coordinates.",
    });

    try {
      const position = await new Promise<GeolocationPosition>((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(resolve, reject, {
          enableHighAccuracy: true,
          timeout: 10000, // 10 seconds timeout
          maximumAge: 0, // Force fresh location
        });
      });

      const { latitude, longitude } = position.coords;
      toast({
        title: "Coordinates Found",
        description: `Lat: ${latitude.toFixed(4)}, Lon: ${longitude.toFixed(4)}. Getting location description...`,
      });

      // Call Genkit flow for reverse geocoding
      const geoInput: ReverseGeocodeInput = { latitude, longitude };
      const geoResult = await reverseGeocode(geoInput);

      if (geoResult.locationDescription) {
        form.setValue("location", geoResult.locationDescription, { shouldValidate: true });
        toast({
          title: "Location Set!",
          description: `Location automatically set to: ${geoResult.locationDescription}`,
           variant: "default",
        });
      } else {
         toast({
           title: "Reverse Geocoding Failed",
           description: "Could not determine location description from coordinates. Please enter manually.",
           variant: "destructive",
         });
      }

    } catch (error: any) {
      console.error("Error getting location:", error);
      let description = "An unknown error occurred while fetching your location.";
      if (error.code === error.PERMISSION_DENIED) {
        description = "Permission denied. Please enable location services for this site.";
      } else if (error.code === error.POSITION_UNAVAILABLE) {
        description = "Location information is unavailable.";
      } else if (error.code === error.TIMEOUT) {
        description = "The request to get user location timed out.";
      } else if (error.message.includes('reverseGeocode')) {
          description = `Failed to get location description: ${error.message}`;
      }

      toast({
        title: "Location Error",
        description: description,
        variant: "destructive",
      });
    } finally {
      setIsFetchingLocation(false);
    }
  };


  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
        <FormField
          control={form.control}
          name="location"
          render={({ field }) => (
            <FormItem>
               <div className="flex items-center justify-between">
                  <FormLabel className="flex items-center gap-2">
                    <MapPin className="h-5 w-5 text-primary" />
                    Location
                  </FormLabel>
                   <Button
                     type="button"
                     variant="outline"
                     size="sm"
                     onClick={handleGetCurrentLocation}
                     disabled={isFetchingLocation || isLoading}
                     className="flex items-center gap-1 text-xs"
                   >
                     {isFetchingLocation ? (
                       <Loader2 className="h-3 w-3 animate-spin" />
                     ) : (
                       <LocateFixed className="h-3 w-3" />
                     )}
                     Use Current Location
                   </Button>
                </div>
              <FormControl>
                <Input placeholder="e.g., Central Valley, California" {...field} />
              </FormControl>
              <FormDescription>
                Provide the general location or use the button to detect it.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="desiredCrops"
          render={({ field }) => (
            <FormItem>
              <FormLabel className="flex items-center gap-2">
                 <Sprout className="h-5 w-5 text-primary" />
                Desired Crops (Optional)
              </FormLabel>
              <FormControl>
                <Input placeholder="e.g., tomatoes, lettuce, strawberries" {...field} />
              </FormControl>
              <FormDescription>
                List any specific crops you are interested in growing.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

         <FormField
          control={form.control}
          name="soilType"
          render={({ field }) => (
            <FormItem>
              <FormLabel className="flex items-center gap-2">
                 {/* Placeholder Soil Icon - Consider a more specific icon */}
                 <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="text-primary"><path d="M2 12C2 6.5 6.5 2 12 2s10 4.5 10 10c0 2.7-1.1 5.2-2.9 7.1L12 22l-7.1-2.9C3.1 17.2 2 14.7 2 12Z"/><path d="M12 16s-4-3-4-6 4-4 4-4 4 1 4 4-4 6-4 6Z"/></svg>
                 Soil Type (Optional)
              </FormLabel>
              <FormControl>
                <Input placeholder="e.g., Loam, Clay, Sandy" {...field} />
              </FormControl>
               <FormDescription>
                Specify the soil type if known. AI will estimate if left blank.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

         <FormField
          control={form.control}
          name="climateConditions"
          render={({ field }) => (
            <FormItem>
              <FormLabel className="flex items-center gap-2">
                <CloudSun className="h-5 w-5 text-primary" />
                Climate Conditions (Optional)
              </FormLabel>
              <FormControl>
                <Textarea placeholder="e.g., Avg Temp: 20Â°C, Rainfall: 500mm/year" {...field} />
              </FormControl>
              <FormDescription>
                Describe the typical climate or provide specific data. AI will estimate if left blank.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="historicalYieldData"
          render={({ field }) => (
            <FormItem>
              <FormLabel className="flex items-center gap-2">
                <BookOpen className="h-5 w-5 text-primary" />
                Historical Yield Data (Optional)
              </FormLabel>
              <FormControl>
                <Textarea placeholder="e.g., Corn: 5 tons/acre (2023), Soybeans: 2 tons/acre (2022)" {...field} />
              </FormControl>
               <FormDescription>
                Provide past yield data if available. AI will estimate if left blank.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

         <FormField
          control={form.control}
          name="otherParameters"
          render={({ field }) => (
            <FormItem>
              <FormLabel className="flex items-center gap-2">
                 {/* Placeholder Other Icon */}
                 <svg xmlns="http://www.w3.org/2000/svg" width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="text-primary"><circle cx="12" cy="12" r="10"/><path d="M12 16v-4"/><path d="M12 8h.01"/></svg>
                Other Relevant Parameters (Optional)
              </FormLabel>
              <FormControl>
                <Textarea placeholder="e.g., Water availability, pest history, market prices" {...field} />
              </FormControl>
              <FormDescription>
                Include any other factors that might influence crop choice.
              </FormDescription>
              <FormMessage />
            </FormItem>
          )}
        />

        <Button type="submit" disabled={isLoading || isFetchingLocation} className="w-full bg-primary hover:bg-primary/90 text-primary-foreground">
          {isLoading ? (
            <>
              <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              Predicting...
            </>
          ) : (
            <>
              <BrainCircuit className="mr-2 h-4 w-4" />
              Predict Suitable Crops
            </>
          )}
        </Button>
      </form>
    </Form>
  );
}
